import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import * as S from './Style';
import Logo from '../../Components/Logo/Logo';
import Instance from '../../Utils/api/axiosInstance';
import axios from 'axios';
import { useCookies } from 'react-cookie';
import {useAuth} from "../../Utils/api/AuthContext";

const Login = () => {
  const navigate = useNavigate();
  const { setLoggedIn } = useAuth();
  const {
    register,
    handleSubmit,
    formState: { isSubmitting },
  } = useForm();

  const [cookies, setCookie] = useCookies(['JSESSIONID']);

  const onSubmit = (data: any) => {
    axios.post('/ideApi/api/member/login', data, { headers: { 'Content-Type': 'multipart/form-data' } })
      .then((response) => {
        if (response.status === 200) {
          console.log('React:  ');
          setLoggedIn(true);
          localStorage.setItem('isLoggedIn', 'true');
          // console.log('cookie ' + response.headers['jsessionid']);
          setCookie('JSESSIONID', response.headers['jsessionid'], { path: '/ideApi' });
          navigate('/');
        }
      })
      .catch((error) => {
        console.log(error.data);
        alert('   .');
      });
  };

  return (
    <S.Login>
      <div className="w">
        <Logo isAdmin={false} /> {/*      */}
        <div>
          <form onSubmit={handleSubmit(onSubmit)}>
            <input {...register('memberId')} type="text" name="memberId" placeholder="" required />
            <input {...register('password')} type="password" name="password" placeholder="" required />
            <button type="submit" disabled={isSubmitting}>
              
            </button>
          </form>
          <p className="link">
             ? <Link to="/signup"></Link>
          </p>
          <p className="link">
            / ? <Link to="/find">/ </Link>
          </p>
        </div>
      </div>
    </S.Login>
  );
};

export default Login;
