import React, { useContext, useState } from 'react';
import { Link } from 'react-router-dom';
import * as S from './Style';
import Logo from '../Logo/Logo';
import { ThemeContext, ThemeContextProps } from '../../Container/Home/Home';
//   ThemeContextProps(type )   . => import  
import Instance from '../../Utils/api/axiosInstance';
import axios from 'axios';
import LogoutBtn from '../LogoutBtn';

const Header = () => {
  const { toggleTheme, isLight } = useContext(ThemeContext); //  : App.tsx  
  let [isLoggedIn, setLogState] = useState(true); //   
  let [logMemberName, setLogMemberName] = useState(); // ()

  //  () 
  axios.get('/ideApi/api/member/', { withCredentials: true })
    .then((response) => {
      // console.log(response);
      if (response.status === 200) {
        if (response.data === '') {
          //   
          setLogState(true);
        } else {
          //   
          // console.log(':' + response.data);
          setLogMemberName(response.data);
          setLogState(false);
        }
      }
    })
    .catch((error) => {
      console.log(error.data);
      setLogState(true);
    });

  return (
    <S.Header>
      <div className="left">
        <Logo isAdmin={false} />
        <div className="darkmodew">
          {/* theme button: dark mode(default) */}
          <button type="button" onClick={toggleTheme} className={isLight ? 'dark' : 'light'}>
            <span>{/** /   */}</span>
          </button>
        </div>
      </div>
      <div className="right">
        {isLoggedIn ? (
          <div className="guest">
            <Link to="/login"></Link>
            <Link to="/signup"></Link>
          </div>
        ) : (
          <div className="member">
            <Link to="/myaccount">{logMemberName}</Link> 
            <LogoutBtn />
          </div>
        )}
      </div>
    </S.Header>
  );
};

export default Header;
