import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import * as S from './Style';
import Logo from '../../Components/Logo/Logo';
import Instance from '../../Utils/api/axiosInstance';
import { useForm } from 'react-hook-form';
import axios from 'axios';

const MyAccount = () => {
  const navigate = useNavigate();
  const [memberInfo, setMemberInfo] = useState({
    memberId: '',
    memberName: '',
    email: '',
    password: '',
    passwordCheck: '',
    createMemberDate: [],
  }); //   get
  const {
    register,
    handleSubmit,
    reset,
    formState: { isSubmitting },
  } = useForm({
    defaultValues: memberInfo,
  });

  useEffect(() => {
    //   
    axios.get('/ideApi/api/member/myaccount', { withCredentials: true })
      .then((response) => {
        // console.log(response);
        if (response.status === 200) {
          if (response.data === '') {
            //   
            alert('. \n  .');
            navigate('/login');
          } else {
            //   
            console.log('.');
            setMemberInfo(response.data);
            reset(response.data);
          }
        }
      })
      .catch((error) => {
        console.log(error.data);
        navigate('/login');
      });
  }, []);

  //   
  const onSubmit = (data: any) => {
    axios.post('/ideApi/api/member/myaccount', data, { headers: { 'Content-Type': 'application/json' } })
      .then((response) => {
        if (response.status === 200) {
          alert('  .');
          window.location.reload();
        }
      })
      .catch((error) => {
        console.log(error.data);
        alert(' .    .\n   .');
      });
  };

  //  
  const deleteAccount = () => {
    if (window.confirm('  ?')) {
      axios.delete(`/ideApi/api/member/deleteId/${memberInfo.memberId}`)
        .then((response) => {
          alert('  .');
          navigate('/');
        })
        .catch((error) => {
          console.error(error);
        });
    }
  };

  return (
    <S.Member>
      <div className="w">
        <Logo isAdmin={false} />
        <div>
          <form onSubmit={handleSubmit(onSubmit)}>
            <input {...register('createMemberDate')} type="hidden" name="createMemberDate" value={memberInfo.createMemberDate} disabled />
            <input
              {...register('memberId')}
              type="text"
              name="memberId"
              placeholder=""
              value={memberInfo.memberId}
              defaultValue={memberInfo.memberId}
              readOnly
            />
            <input
              {...register('memberName')}
              type="text"
              name="memberName"
              placeholder=""
              value={memberInfo.memberName}
              defaultValue={memberInfo.memberName}
              readOnly
            />
            <input {...register('password')} type="password" name="password" placeholder="" required />
            <input {...register('passwordCheck')} type="password" name="passwordCheck" placeholder=" " required />
            <input {...register('email')} type="text" name="email" placeholder="" defaultValue={memberInfo.email} required />
            <button type="submit" disabled={isSubmitting}>
              
            </button>
          </form>
          <p className="link">
            <button className="delBtn" onClick={deleteAccount}>
              
            </button>
          </p>
        </div>
      </div>
    </S.Member>
  );
};

export default MyAccount;
